    font-family: Raleway;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin-top: 15px;
}

.content {
    color: #0D0D0D;
     margin-top: 15px;
    font-family: Montserrat;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

/* UserMap.css */

.user-map__container {
  width: 100vw;
  max-width: 900px;
  aspect-ratio: 839 / 768;
  position: relative;
  margin: 0 auto 24px auto;
  border: 2px dashed #4475ff44;
  background: #fff;
  border-radius: 18px;
  overflow: hidden;
  box-shadow: 0 4px 28px #a3b2ff11;
  transition: box-shadow 0.2s;
}
.user-map__container:hover {
  box-shadow: 0 8px 32px #94b3ff22;
}

.user-map__img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
  border-radius: 16px;
  pointer-events: none;
  user-select: none;
  filter: brightness(0.99) saturate(1.04);
}

.user-map__point {
  position: absolute;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  box-shadow: 0 2px 12px #0002;
  user-select: none;
  transition: transform .15s, box-shadow .2s;
  font-size: 16px;
  z-index: 20;
  border: 2.5px solid #fff;
  background: #4475ff;
}
.user-map__point--start { background: #3de579; border-width: 3px; }
.user-map__point--end { background: #ffa500; border-width: 3px; }
.user-map__point:active { transform: scale(1.10); }

@media (max-width: 600px) {
  .user-map__container {
    max-width: 100vw;
    border-radius: 10px;
    min-width: 0;
  }
  .user-map__point {
    font-size: 18px;
    width: 36px !important;
    height: 36px !important;
  }
}

@media (min-width: 601px) {
  .user-map__point {
    width: 32px !important;
    height: 32px !important;
  }
}

/* SVG линии */
.user-map__svg-polyline {
  stroke: #ff4056;
  stroke-width: 1.2;
  stroke-dasharray: 4, 2;
  fill: none;
  filter: drop-shadow(0 0 2px #ff7fa333);
  transition: stroke-width 0.15s;
}

@media (max-width: 600px) {
  .user-map__svg-polyline {
    stroke-width: 0.7;
  }
}

/* Название маршрута */
.user-map__route-name {
  display: inline-block;
  padding: 3px 10px;
  border-radius: 12px;
  background: #eef3fd;
  color: #294175;
  font-size: 1rem;
  margin-bottom: 12px;
  font-weight: 500;
  box-shadow: 0 2px 8px #b2d5ff13;
}

/* Модалка точек */
.user-map__modal {
  border-radius: 18px !important;
  box-shadow: 0 8px 36px #264e1515 !important;
}
.user-map__modal-content {
  padding: 20px 24px 30px 24px;
  border-radius: 18px;
  position: relative;
}
.user-map__modal-title {
  color: #40be3b;
  font-weight: bold;
  text-align: center;
  font-size: 21px;
  margin-top: 8px;
  margin-bottom: 15px;
}
.user-map__modal-imgbox {
  border-radius: 12px;
  overflow: hidden;
  margin-bottom: 18px;
  height: 82px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f2f2f2;
}
.user-map__modal-img {
  width: 100%;
  height: 82px;
  object-fit: cover;
  display: block;
}
.user-map__modal-info {
  font-size: 17px;
}
.user-map__modal-row {
  display: flex;
  gap: 8px;
  margin-bottom: 2px;
}
.user-map__modal-name {
  color: #40be3b;
  font-weight: 500;
  min-width: 56px;
}
.user-map__modal-content-btn {
  background: #40be3b;
  color: #fff;
  font-weight: 500;
  border-radius: 8px;
  text-transform: none;
  padding: 7px 32px;
  font-size: 16px;
  margin-top: 18px;
}
.user-map__modal-content-btn:hover {
  background: #2fa42b;
}

